<template>
  <div id="app" class="notebook">
    <h3  class="head">记事本：</h3>
    <p><strong>添加需做事项：</strong></p>
    <input v-model="newItem" @keyup.enter="addNew" style="background-color: #CD950C" />
    <ul>
      <li v-for="(item, index) in items"
      :key="index"
      :class="{finished: item.isFinished}"
      @click="toggleFinish(item)"
      @contextmenu="deleteItem(index)"
      >{{item.label}}</li>
    </ul>
  </div>
</template>

<script>
  document.oncontextmenu=function(e){return false;};
  import Store from './store'
  export default {
    data () {
      return {
        items: Store.fetch(),
        newItem: ''
      }
    },
    watch: {
      items: {
        handler (items){
          Store.save(items)
        },
        deep:true
      }
    },
    methods: {
      toggleFinish (item) {
        if(item.isFinished===true){
          var mess=confirm("是否继续该事项？")
          if(mess===true){
            item.isFinished=!item.isFinished
          }
        }
        else{
          var mess=confirm("确认已完成该事项？")
          if(mess===true){
            item.isFinished=!item.isFinished
          }
        }
      },
      addNew () {
        this.items.push({
          label: this.newItem,
          isFinished: false
        })
        this.newItem= ''
      },
      deleteItem (index) {
        var mess1=confirm("确认删除该事项？")
        if(mess1===true){
          this.items.splice(index, 1)
        }
      }
    }  
  }
</script>

<style>
  .finished {
    text-decoration:line-through;
  }
  .head{
    text-align: center;
    color: #080808;
  }
  .notebook{
    width: 500px;
    height: 800px;
    background-color: #969696;
    margin: 0 auto;
  }
</style>



